html, body {
  height: 100%;
  min-height: 100%;
  overflow-x: hidden;
  background-image: url("/img/bg.png"); }

input {
  cursor: pointer;
  outline: none; }

.container {
  position: relative;
  width: 100%;
  height: 100%;
  max-width: 90vw;
  margin: 0 auto; }

.flex-row {
  display: flex;
  flex-direction: row;
  align-items: center; }

.flex-column {
  display: flex;
  flex-direction: column;
  align-items: center; }

.col-12 {
  flex: 0 0 100%; }

.col-6 {
  flex: 0 0 50%; }

.LoginPage {
  z-index: -1; }
  .LoginPage h1 {
    color: green; }
  .LoginPage .LoginBox {
    height: 400px;
    display: flex;
    background-color: lightcyan;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    box-shadow: 0 0 5px;
    width: 40%;
    z-index: 100; }
    .LoginPage .LoginBox .LoginForm {
      display: flex;
      flex-direction: column;
      width: 50%; }
      .LoginPage .LoginBox .LoginForm h3 {
        margin: 0;
        text-align: center;
        color: #119111; }
      .LoginPage .LoginBox .LoginForm p {
        text-align: center;
        font-weight: bold;
        margin: 5px; }
      .LoginPage .LoginBox .LoginForm .loginButton {
        cursor: pointer;
        width: 100%;
        border: 1px solid #917474;
        border-radius: 20px;
        height: 30px;
        font-weight: bold;
        background: #057a05;
        margin-top: 10px;
        margin-bottom: 10px;
        display: block;
        color: white;
        transition: .3s all linear; }
      .LoginPage .LoginBox .LoginForm .loginButton:hover {
        background: lightgreen;
        color: red; }
      .LoginPage .LoginBox .LoginForm .selected {
        color: red;
        background-color: lightgreen; }
      .LoginPage .LoginBox .LoginForm .error {
        color: red; }

input[type='text'], input[type='password'] {
  background-color: white;
  box-shadow: 0 0 3px;
  border-radius: 10px; }

.loaderBox {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: absolute;
  transform: translate(-50%, -50%);
  left: 50%;
  top: 50%; }
  .loaderBox p {
    font-size: 20px;
    font-weight: bold;
    text-align: center; }
  .loaderBox .loader {
    width: 60%; }

[draggable="true"] {
  -moz-user-select: none;
  -webkit-user-select: none;
  -webkit-user-drag: element;
  cursor: move; }
